<template>
  <!-- 运单总数 -->
  <chart :options="option" ref="chartsAll" />
</template>

<script>
  import CommonWrapper from '@/components/commonWrapper'
  import ECharts from 'vue-echarts/components/ECharts'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/chart/bar'
  import 'echarts/lib/component/polar'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/title'
  import 'echarts/lib/component/dataZoom'

  export default {
    name: 'chartsAll',
    mounted () {
          this.selfAdaption ()
    },
    data () {
      return {
      option:{
          color: ['#3EAFF7','#F5B366','#4FDAD2','#F3D38C','#C9F0F4'],
          tooltip: {
              trigger: 'item',
              formatter: "{a} <br/>{b}: {c} ({d}%)"
          },
          legend: {
              orient: 'vertical',
              x: 'left',
              data:[]
          },
          series: [
              {
                  name:'货物总数',
                  type:'pie',
                  radius: ['50%', '70%'],
                  avoidLabelOverlap: false,
                  label: {
                      normal: {
                          show: false,
                          position: 'center'
                      },
                      emphasis: {
                          show: true,
                          textStyle: {
                              fontSize: '30',
                              fontWeight: 'bold'
                          }
                      }
                  },
                  labelLine: {
                      normal: {
                          show: false
                      }
                  },
                  data:[]
                }
              ]
          },
      }
    },
    props: {
      getSeries: {
        type: Array
      }
    },

    watch: {
      getSeries(newVal) {
        if(newVal && newVal.length > 0){
          this.option.legend.data = []
          this.option.series[0].data = []
          newVal.map(item =>{
            this.option.legend.data.push(item.merchartName)
            this.option.series[0].data.push({value:item.productTotalNum,name:item.merchartName})
          })
        }else{
          this.option.legend.data = []
          this.option.series[0].data = []
        }
      }
    },
    components: { CommonWrapper, chart: ECharts },
    methods:{
      selfAdaption ()  {
        let _this = this ;
        setTimeout(() => {
            window.addEventListener('resize', function () {
                _this.$refs.chartsAll.resize();
            })
          }, 10)
      }
    }
  }
</script>

<style scoped>
.chartsAll{
  height:100%;
  width: 100%;
}
</style>



